.todoList {
  width: 40%;
  height: 100vh;
  border-right: 1px solid #a9a9a9;
}

.item {
  height: 50px;
  padding: 5px 10px;
  border-right: 1px solid #a9a9a9;
  border-bottom: 1px solid #a9a9a9;
  overflow: hidden;
}

.textItem {
  font-family: 'Consolas';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.02;
  margin-bottom: 5px;
  color: #212121;
}

.itemId {
  font-size: 7px;
  color: #212121;
}

.notes {
  font-family: 'Consolas';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.3;
  margin-top: 50px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  color: #212121;
}

@media screen and (min-width: 768px) {
  .item {
    height: 70px;
    padding: 8px 16px;
  }

  .textItem {
    font-size: 16px;
    margin-bottom: 8px;
  }

  .itemId {
    font-size: 12px;
  }

  .notes {
    font-size: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .item {
    height: 90px;
    padding: 10px 20px;
  }

  .textItem {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .itemId {
    font-size: 12px;
  }

  .notes {
    font-size: 28px;
    margin-top: 70px;
  }
}
